<template>
    <el-header>
        <TopNavBar :curId="-1" />
    </el-header>
    <el-container>
        
        <div class="form-container">
            <!-- <form @submit.prevent="submitForm">
                <div v-for="(value, key) in formData" :key="key" class="form-group">
                    <label :for="key">{{ key }}</label>
                    <input :id="key" v-model="formData[key]" type="text" class="form-control" />
                </div>
                <button type="submit" class="btn btn-primary">Submit</button>
            </form> -->
        <el-form :model="formData" @submit.prevent="submitForm">
            <el-form-item v-for="(value, key) in formData" :key="key" :label="key">
                <el-input v-model="formData[key]" :id="key" disabled="true"></el-input>
            </el-form-item>
            <el-form-item>
                <!-- <el-button type="primary" @click="submitForm">Submit</el-button> -->
            </el-form-item>
        </el-form>
        </div>

    </el-container>
</template>

<script>
import TopNavBar from './TopNavBar.vue';
import axios from 'axios';

export default {
    components: {
        TopNavBar
    },
    data() {
        return {
            formData: {}
        };
    },
    created() {
        const num = this.$route.query.num;
        this.fetchData(num);
    },
    methods: {
        fetchData(num) {
            console.log('Fetching data for num:', num);
            axios.get(`/api/get_by_num.php?num=${num}`)
                .then(response => {
                    this.formData = response.data;
                })
                .catch(error => {
                    console.error('Error fetching data:', error);
                });
        },
        submitForm() {
            // Handle form submission
            console.log('Form submitted:', this.formData);
        }
    }
};
</script>

<style scoped>
.form-container {
    margin: 20px auto;
    width: 50%;
}
.form-group {
    margin-bottom: 15px;
}
</style>